body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #0f6ab4;
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.button {
  color: #fff;
  background-color: #5cb85c;
  text-align: center;
  border: 1px solid transparent;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  overflow: visible;
  text-transform: none;
  background-image: none;
  webkit-appearance: button;
  font-size: 18px;
}

.button:hover {
  background: #383;
}

tr:nth-child(even){
    background-color: #f2f2f2;
}

tr:hover {
    background-color: #ddd;
}

table {
    border-collapse: collapse;
    width: 85%;
    margin: 0 auto;
}

td, th {
    border: 1px solid #ddd;
    padding-left: 15px;
    padding-right: 15px;
}

th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: rgba(12, 103, 172, 0.6);
    color: white;
}
.register-row {
    display: -webkit-flex;
    display: flex;
    padding-bottom: 10px;
    width: 40%;
    margin: 0 auto;
    padding-right: 50px;
}

.register-label {
    -webkit-flex: 20% 1;
            flex: 20% 1;
    text-align: right;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 20px;
}

.register-input {
    -webkit-flex: 60% 1;
            flex: 60% 1;
}

.register-invalid {
    position: relative;
    color: red;
    font-size: 11px;
    margin-bottom: 2px;
    margin-top: 0px;
    margin-left: -55px;
    text-align: center;
}

.register-invalid-password {
    position: relative;
    color: red;
    font-size: 11px;
    padding-left: 70px;
    margin-bottom: 2px;
    margin-top: 0px;
    text-align: center;
}

.register-invalid-confirm {
    position: relative;
    color: red;
    font-size: 11px;
    padding-left: 5px;
    margin-bottom: 2px;
    margin-top: 0px;
    text-align: center;
}
